.header {
    max-width: 880px;
    width: 100%;
    min-height: 74px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
    box-sizing: border-box;
    margin: 45px auto 0;
}

@media screen and (max-width: 320px) {
    .header {
        margin: 28px auto 0;
        min-height: 56px;
    }
}